<template>
  <div id="map">
  </div>
</template>

<style type="text/css">
  @import url(/static/styles/leaflet.css)
</style>
<script>
  import {mapServerUrl} from '@/utils/staticUrl'
  import L from 'leaflet'
  import Lesri from 'esri-leaflet'
  export default {
    data () {
      return {
      	mapServerUrl:`${mapServerUrl}`
      }
    },
    mounted () {
      var map = L.map('map').setView([39.9299857781,116.395645038], 5);
      Lesri.basemapLayer('Streets').addTo(map);
      // Lesri.dynamicMapLayer({
      //   url: this.mapServerUrl,
      //   opacity : 1,
      //   useCors: true,
      //   transparent: false
      // }).addTo(map);
    }
  }
</script>



